import { AxiosError } from 'axios';
import { FieldValues } from 'react-hook-form';
import { $authHost, $host } from './index';

export const regCheck = async (arg: FieldValues): Promise<any> => {
	try {
		const { data } = await $host.post('api/usercheck/registration', {
			...arg[0],
		});
		return data;
	} catch (err) {
		if (err instanceof AxiosError) return err.response?.data;
		else return err;
	}
};

export const loginCheck = async (arg: FieldValues): Promise<any> => {
	try {

		const { data } = await $host.post('api/usercheck/login', {
			...arg[0],
		});
		return data;
	} catch (err) {
		if (err instanceof AxiosError) {
			return err.response?.data;
		} else return err;
	}
};

export const validcode = async (): Promise<any> => {
	try {
		const { data } = await $host.get('api/usercheck/validcode');
		return data;
	} catch (err) {
		if (err instanceof AxiosError) {
			throw err?.response?.data;
		} else return err;
	}
};
